.hero {
  height: 813px;
  background-image: url("../assets/images/global/hero.png");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; /* Dodano dla pełnego pokrycia diva obrazem */

  /* Dodano prefiksy dla różnych przeglądarek */
  -webkit-background-size: cover;
  -moz-background-size: cover;
  position: -webkit-sticky;

  /* Ustawienie dla przeglądarek WebKit (Safari) */
  @supports (-webkit-touch-callout: none) {
    background-image: url("../assets/images/retina/hero@2x.png");
  }
}
